























































































































.nav {
  width: 250px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  min-height: 100vh;
  transition: left 0.5s;
  z-index: 9;
  h1 {
    padding: 20px 10px;
    font-size: 25px;
  }
  .list {
    .item {
      font-size: 15px;
      a {
        padding: 20px;
        display: block;
        color: #e8e8e8;
        border: 1px solid transparent;
        transition: all 500ms;
        .icon {
          width: 20px;
          height: 20px;
          margin-right: 10px;
          color: #b1abab;
          vertical-align: bottom;
        }
      }
      .router-link-active {
        background-color: #fff;
        color: #d43b33;
        .icon {
          color: #d43b33;

        }
      }
    }
  }
}
.nav-top {
  width: 100vw;
  color: #fff;
  background-color: #d43b33;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  display: none;
  h1 {
    font-size: 25px;
  }
  button {
    animation-iteration-count: infinite;
  }
  .icon {
    font-size: 35px;
    font-weight: bold;
    color: #fff;
  }
}
.swing-animate {
}

.btnpdf {
  position: absolute;
  bottom: 30px;
  left: 30px;
  padding: 14px 20px;
  background: #c3b327;
  border-radius: 5px;
  animation-iteration-count: infinite;
  animation-duration: 2s;
  a {
    width: 100%;
    height: 100%;
    display: block;
    color: #fff;
  }
}

.slidenav-enter,
.slidenav-leave-to {
  left: -100%;
}
.slidenav-enter-active,
.slidenav-leave-active {
  transition: left 0.5s;
  left: 0;
}

@media screen and (max-width: 768px) {
  .nav {
    position: fixed;
    left: -100%;
    background-color: #d43b33;
    width: 231px;
    .list {
      .item a {
        padding: 14px 20px;
        .icon {
          margin-right: 5px;
          color: #fff;
        }
      }
    }

    &.show {
      left: 0;
    }
  }
  .nav-top {
    display: block;
  }
}
