




















































































































































































































.blog {
  color: #fff;
  // margin: 50px;
}

  .card {
    padding: 20px 20px 10px;
    background-color: rgba(0, 0, 0, 0.4);
    border: none;
    position: relative;

    .title {
      cursor: pointer;
      font-size: 19px;
      margin-bottom: 5px;
      a {
        color: inherit;
        .icon {
          width: 25px;
          height: 25px;
        }
      }
    }
    article {
      color: #c1c1c1;
      font-size: 14px;
      .prev {
        h4 {
          margin: 3px 0;
        }
        ul {
          margin-left: 14px;
        }
        margin-bottom: 10px;
      }
      pre {
        font-family: "微软雅黑";
        font-size: 12px;
        font-size: 12px;
        border: 1px solid #696969;
        box-sizing: content-box;
        padding: 5px;
        margin-top: 5px;
        width: 100%;
        overflow: hidden;
      }
      h4 {
        margin: 5px 0;
      }
      ul {
        margin-left: 14px;
      }
    }
    .keyword {
      font-size: 12px;
      margin-bottom: 10px;
      color: #ada5a5;

      li {
        margin-right: 10px;
      }
    }
    .link {
      margin-top: 15px;
      a {
        color: #fff;
        .icon {
          width: 25px;
          height: 25px;
        }
      }
    }
  }
@media screen and (max-width:500px){
.card .title{
  font-size: 16px;
}
}
