

































































































































.skill {
  color: #fff;
  padding: 50px 40px;
}
.content {
  margin-top: 90px;
  .box {
    .item {
      width: 190px;
      padding: 10px;
      font-size: 14px;
      margin-bottom: 20px;
      min-height: 275px;
    }
    &:nth-child(even) .item {
      background: rgba(5, 146, 92, 0.986);
    }
    &:nth-child(odd) .item {
      background: rgba(255, 255, 255, 0.3);
    }
    li {
      margin: 15px 0;
    }
    h1 {
      color: rgba(255, 255, 255, 0.3);
    }
  }
}
@media screen and (max-width: 500px) {
  .skill .content {
    margin: 58px;
    .box {
      transform: scale(1);
      transition: transform 0.5s;
      &.scale {
        transform: scale(1.2);
      }
    }
  }
}
