.leftMove-leave-to {
  transform: translateX(-100%);
}
.leftMove-enter-active,
.leftMove-leave-active {
  transition: transform 1s;
}
.leftMove-enter-to,
.leftMove-leave {
  transform: translateX(0);
}

.rightMove-enter,
.rightMove-leave-to {
  transform: translateX(100%);
}
.rightMove-enter-active,
.rightMove-leave-active {
  transition: transform 1s;
}
.rightMove-enter-to,
.rightMove-leave {
  transform: translateX(0);
}

.proslide-enter {
  transform: translateY(100%);
}
.proslide-leave-to {
  transform: translateY(-100%);
}
.proslide-enter-active,
.proslide-leave-active {
  transition: transform 1s;
}
.proslide-enter-to,
.proslide-leave {
  transform: translateY(0);
}

.portfolio {
  color: #fff;
  width: 100%;
}
.item-wrapper {
  overflow: hidden;
  height: calc(100vh - 50px);
  transition: transform 0.5s;
  position:relative;
  .item {
    width: 100%;
  }
  &.offsetTop {
    transform: translateY(-100%);
  }
  
}

.item-wrapper .content {
  overflow: scroll;
  .left {
    width: calc(100% - 317px);
    min-width: 357px;
    height: 537px;
    background-color: rgba(0, 0, 0, 0.6);
    padding: 40px;
    position: relative;
    .title {
      h2 {
        position: relative;
        font-size: 25px;
        color: rgba(255, 255, 255, 0.6);
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -10px;
          width: 80%;
          height: 2px;
          background-color: rgba(212, 59, 51, 1);
        }
      }
    }
    .desc {
      margin-top: 20px;
      font-size: 14px;
      color: rgba(255, 255, 255, 0.5);
    }
    .info {
      width: 88%;
      border: 1px solid #fff;
      color: rgba(199, 187, 187, 1);
      margin: 14px;
      font-size: 13px;

      ol {
        padding: 10px;
        margin: 10px;
        background-color: rgba(255, 255, 255, 0.2);
      }
    }
    .link {
      float: right;
      button {
        padding: 10px 20px;
        background-color: rgb(212, 59, 51);
        margin-right: 10px;
        a {
          color: #fff;
        }
      }
    }
    .slide-icon {
      position: absolute;
      bottom: 0px;
      right: -5px;
      display: none;
      .icon {
        width: 50px;
        height: 50px;
      }
    }
  }
  .right {
    .pic {
      width: 317px;
      height: 537px;
      overflow: hidden;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0);
        z-index: 1;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .item-wrapper .content .left .slide-icon {
    display: block;
  }
}
@media screen and (max-width: 500px) {
  .container .portfolio {
    padding: 0;
    overflow: hidden;
    font-size: 0;
  }
  .item-wrapper {
    height: calc(100vh - 50px);
    .item {
      height: 100%;
    }
    .content {
      position: relative;
      height: 100%;
      display: flex;
      align-items: center;
      .left {
        position: absolute;
        left: 0;
        background-color: transparent;
        height: 100%;
        width: 100%;
        min-width: 100%;
        padding: 35px;
        overflow: hidden;
        .title h2 {
          font-size: 15px;
          &:after {
            width: 80%;
          }
        }
        .tem {
          padding: 20px;
          background: rgba(0, 0, 0, 0.7);
          position: relative;
        }
        .info {
          margin: 16px 0;
          width: 100%;
          font-size: 14px;
          ol {
            margin: 5px;
          }
        }
        .desc {
          font-size: 12px;
        }
        .link {
          float: none;
          button {
          }
        }
      }
      .right {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 0;
        &:after {
          // content: "";
          // position: absolute;
          // left: 0;
          // top: 0;
          // width: 100%;
          // height: 100%;
          // background-color: transparent;
          // z-index: 2;
        }
        .pic {
          position: relative;
        }
      }
    }
  }
}
