























































































.concat {
  color: #fff;
}
.wrapper {
  width: 70%;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
}
.item {
  margin: 5px 16px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  // animation: float 2s ease-out  infinite  ;
  animation-iteration-count: infinite;
}
.uniq {
  width: 80px;
  height: 80px;
  padding: 0;
}
.content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fdfdfd;
  padding: 10px;
  font-size: 12px;
  &.zhihu .icon {
    width: 30px;
    height: 30px;
  }
  .icon {
    width: 40px;
    height: 40px;
  }
  a {
    color:#fdfdfd;
    text-decoration: underline;
  }

  .wei {
    a {
      color: rgb(202, 201, 201);
      padding-bottom: 1px;
      border-bottom: 1px solid rgb(202, 201, 201);
    }
  }
}
@media screen and (max-width: 768px) {
  .wrapper {
    width: 100%;
  }
  .item {
    padding: 5px;
  }
  .content {
    .icon {
      width: 20px;
      height: 20px;
    }
  }
}

@keyframes float {
  0% {
    transform: translate(0px, 0px);
  }
  50%{
    transform: translate(0px, 50px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
