










































































































































































.wait-enter,
.wait-leave-to {
  opacity: 0;
}
.wait-enter-active,
.wait-leave-active {
  transition: opacity 500ms;
  opacity: 1;
}

.message {
  color: #fff;
  .wrapper {
    max-width: 500px;
    margin: 0 auto;
    width: 90%;
  }
  .info {
    text-align: right;
    font-size: 12px;
    color: #d2d2d2;
  }
  .form {
    position: relative;
    form {
      background-color: rgba(0, 0, 0, 0.4);
      * {
        padding: 10px;
        background-color: transparent;
        border: none;
        resize: none;
        color: rgb(199, 199, 199);
      }
      input {
        border-bottom: 1px solid rgb(124, 118, 118);
      }
      button {
        border: none;
        background-color: rgb(190, 78, 72);
      }
    }

    .wait {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: transparent;
      display: flex;
      align-items: center;
      justify-content: center;
      &:after {
        content: "waiting ...";
        width: 100px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #fff;
        background-color: #000;
      }
    }
  }
  .content {
    margin-top: 20px;
    .item{
      padding: 10px;
      background-color: rgba(0, 0, 0, 0.4);
      margin-bottom: 10px;
    }
    .name {
      color: #f18664;
      font-size: 14px;
    }
    .time {
      color: #999;
      font-size: 13px;
    }
    .mes {
      color: #fff;
      font-size: 15px;
      padding: 10px;
    }

    .edit {
      display: flex;
      justify-content: flex-end;
      button {
        padding: 3px 10px;
        background-color: rgba(212, 59, 51, 1);
        color:#fff;
        font-size: 12px;
      }
      button + button {
        background-color: #fff;
        color: #333;
      }
    }
  }
}
@media screen and (max-width: 500px) {
  .message .wrapper {
    .form {
      // *{
      //   padding: 5px
      // }
      textarea {
        height: 50px;
      }
    }
  }
}
