





















































































































































.person {
  width: 100%;
}
.section {
  position: relative;
}
.section2 {
  .header,
  .item {
    display: none;
  }
}

.section1 {
  // display: flex;
  // justify-content: space-between;
  padding: 10% 15%;
  .pic-wrapper {
    position: relative;
    width: 200px;
    height: 200px;
  }
  .pic {
    width: 100%;
    border: 1px solid #fff;
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      border: 10px solid yellow;
      z-index: -1;
      top: 25px;
      left: -25px;
    }
    img {
      width: 100%;
      padding: 5px;
      background: #000;
    }
  }
  h1 {
    margin-top: 15px;
    margin-bottom: 5px;
  }

  hr {
    margin: 20px 0;
  }
  .info-wrapper {
    margin-top: 70px;
    width: 450px;
    position: relative;
  }

  .info {
    background: #000;
    font-size: 14px;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.24);
    color: #fff;
    border: 1px solid #fff;
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      border: 10px solid #32895b;
      bottom: -28px;
      right: -28px;
      z-index: -5;
    }

    .tem {
      margin: 5px;
      padding: 20px 100px;
      background: #353535;
    }
    dl {
      display: flex;
    }

    dt {
      width: 36%;
      margin-bottom: 14px;
      float: left;
      font-weight: bold;
      color: rgb(157, 160, 167);
    }
  }
}

.section2 {
  padding: 45px;

  .header {
    color: #fff;
    position: relative;
    h1:after {
      content: "";
      position: absolute;
      bottom: -16px;
      width: 154px;
      height: 5px;
      background: #b7b717fa;
      left: 0;
    }
  }
  .content {
    margin-top: 70px;
    .item {
      width: 40%;
      margin-bottom: 30px;
    }
    .box {
      background: rgba(7, 156, 73, 0.6);
      color: #fff;
      min-height: 330px;
      padding: 30px 23px;
      &.honor {
        background: rgba(239, 171, 21, 0.6);
      }
      .icon {
        width: 58px;
        height: 58px;
      }
      h1 {
        margin: 10px 0;
      }
      p,
      ol {
        font-size: 14px;
      }
    }
  }
}
@media screen and (max-width: 500px) {
  .section2 {
    .header {
      // margin-top: 50px;
      h1 {
        font-size: 19px;
        border-bottom: 2px solid yellow;
        margin-bottom: 30px;
        margin-left: -20px;
        &:after {
          display: none;
        }
      }
    }
    .content {
      flex-direction: column;
      margin-top: 0;
      .item {
        width: 100%;
        // margin-top: 30px;
        .box {
          min-height: 200px;
          .icon {
            width: 30px;
            height: 30px;
          }
          h1 {
            font-size: 18px;
            margin: 0 0 10px 0;
          }
          ol {
            font-size: 13px;
          }
        }
      }
    }
  }
  .section1 {
    padding: 0 10%;
    .pic-wrapper {
      width: 120px;
      height: 120px;
      margin-top: 20px;
      margin-bottom: 0;
      .pic {
      }
    }
    .info-wrapper {
      width: 220px;
      margin-top: 20px;
      .info {
        .tem {
          padding: 10px;
          h1 {
            margin-top: 0;
            margin-bottom: 0;
            font-size: 20px;
          }

          hr {
            margin: 6px 0;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .section1 .info {
    .tem {
      padding: 20px;
    }
  }
}
